import React, { useState, useImperativeHandle } from 'react'
import Togglable from './Togglable'

const Blog = React.forwardRef(({ blog, updateLikes }, ref) => {
  const [likes, setLikes] = useState(blog.likes)

  const style = {
    margin: '5px 0',
    padding: '5px 10px',
    border: '1px solid black',
  }

  const addLikes = () => {
    setLikes(likes+1)
    updateLikes({
      ...blog,
      likes: likes+1
    })
  }

  useImperativeHandle(ref, () => {
    return {
      addLikes
    }
  })

  return (
    <div key={blog.id} style={style}>
      {blog.title + 'ã€€'}
      <Togglable buttonLabel='view' secondButtonLabel='hide'>
        <div>{blog.url+'\n'} </div>
        <div>{likes+'\n'} <button onClick={addLikes}>LIKE</button> </div>
        <div>{blog.author} </div>
      </Togglable>
    </div>
  )
}
)
export default Blog
