import React, { useState, useEffect } from 'react'

import BlogList from './components/BlogList'
import BlogForm from './components/BlogForm'
import LoginForm from './components/LoginForm'
import Message from './components/Message'
import Togglable from './components/Togglable'

import blogService from './services/blogs'
import loginService from './services/login'

import './App.css'
import blogs from './services/blogs'

const App = () => {
  const [blogs, setBlogs] = useState([])
  const [username, setUsername] = useState(null)
  const [errorMessage, setErrorMessage] = useState({ message: null, error: false })

  useEffect(() => {
    blogService.getAll().then(blogs =>
      setBlogs( blogs )
    )
  }, [])

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedInBlogUser')
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      setUsername(user)
      blogService.setToken(user.token)
    }
  }, [])

  const handleLogin = async (loginObject) => {
    try {
      const user = await loginService.login(loginObject)
      window.localStorage.setItem('loggedInBlogUser', JSON.stringify(user))
      await blogService.setToken(user.token)

      setUsername(user)
    } catch (err) {
      setErrorMessage({ message: 'Wrong username or password', status: true })
      setTimeout(() => {
        setErrorMessage({ message: null, error: false })
      }, 3000)
    }
  }

  const handleLogout = async (event) => {
    event.preventDefault()

    window.localStorage.clear()
    setUsername(null)

    setErrorMessage({ message: 'Logout success', status: false })
    setTimeout(() => { setErrorMessage({ message: null, error: false }) }, 3000)
  }

  const blogFormRef = React.createRef()

  const handleBlogSubmit = async (blogObject) => {
    try {
      blogFormRef.current.toggleVisibility()
      const addedBlog = await blogService.create(blogObject)
      setBlogs(blogs.concat(addedBlog))

      setErrorMessage({ message: `a new blog ${blogObject.title} added`, status: false })
      setTimeout(() => { setErrorMessage({ message: null, error: false }) }, 3000)

    } catch (err) {
      setErrorMessage({ message: `${err}`, status: true })
      setTimeout(() => { setErrorMessage({ message: null, error: false }) }, 3000)
    }
  }

  const addLikesRef = React.createRef()

  const updateLikes = async (blogObject) => {
    try {
      addLikesRef.current.addLikes()
      await blogService.addLikes(blogObject.id, blogObject)
      await setBlogs(blogs.map((blog) => blog.id !== blogObject.id ? blog : blogObject))
    } catch (err) {
      setErrorMessage(err)
    }
  }


  return (
    <div>
      <h2>BLOGS</h2>
      <Message message={errorMessage} />
      {username !== null ?
        <>
          <div>
            {username.username.toUpperCase()} LOGGED INã€€
            <button onClick={handleLogout}>LOGOUT</button>
          </div>
          <Togglable buttonLabel='NEW POST' ref={blogFormRef} secondButtonLabel='CANCEL'>
            <h3>CREATE NEW</h3>
            <BlogForm handleSubmit={handleBlogSubmit}/>
          </Togglable>
          <BlogList blogs={blogs} updateLikes={updateLikes} Sref={addLikesRef}/>
        </> :
        <Togglable buttonLabel='LOGIN' secondButtonLabel='CANCEL'>
          <LoginForm handleLogin={handleLogin}/>
        </Togglable>
      }
    </div>
  )
}

export default App