import React, { useState } from 'react'

const BlogForm = ({ handleSubmit, ref }) => {
  const [title, setTitle] = useState('')
  const [author, setAuthor] = useState('')
  const [url, setUrl] = useState('')

  const handleChangeTitle = (event) => {
    setTitle(event.target.value)
  }

  const handleChangeAuthor = (event) => {
    setAuthor(event.target.value)
  }

  const handleChangeUrl = (event) => {
    setUrl(event.target.value)
  }

  const addBlog = (event) => {
    event.preventDefault()

    handleSubmit({
      title: title,
      author: author,
      url: url,
    })

    setTitle('')
    setAuthor('')
    setUrl('')
  }

  return (
    <form onSubmit={addBlog}>
      TITLE<br />
      <input type="text" value={title} onChange={handleChangeTitle} autoComplete="off"/><br />
      AUTHOR<br />
      <input type="text" value={author} onChange={handleChangeAuthor} autoComplete="off"/><br />
      URL<br />
      <input type="text" value={url} onChange={handleChangeUrl} autoComplete="off" /><br />
      <input type="submit" value="POST" />
      <br />
      <br />
    </form>
  )
}

export default BlogForm